import { Callbacks } from '../../callbacks';
import { ErrorLevel } from '../../errorlevel';
import { IPlayer } from '../../iplayer';
import { MediaErrorExt } from '../../media-error-ext';
import { PlayerEvent, PlayModes } from '../../player';
import { PlayerFactory } from '../../player-factory';
import { PlayPromiseInfo } from '../../play-promise-info';
import { QualityBoundaries } from '../../quality-boundaries';
import { QualityLevel } from '../../quality-level';
import { BrowserName, BrowserOS, isBrowserVersion } from '../../utils';

import { logger, SourceSet } from 'vchat-core';
import { HlsJsPlayerConfig } from './hlsjs-player-config';
import * as Hls from 'hls.js';

const defaultQualityBoundaries: QualityBoundaries = {
    low: 0,
    medium: 250,
    good: 500
};

const defaultWaitingTimeout = 3000;

const ABORT_ERROR = 'AbortError';
const NOT_ALLOWED_ERROR = 'NotAllowedError';
const NS_ERROR_NOT_AVAILABLE = 'NS_ERROR_NOT_AVAILABLE';

/**
 * A player for HLS streams using the hls.js JavaScript library.
 * supported format: hls
 * name:              HLSJS
 */
export class HlsJsPlayerFactory implements PlayerFactory {
    public readonly format = 'hls';

    private config: HlsJsPlayerConfig;

    constructor(config?: HlsJsPlayerConfig) {
        this.config = config || {};
    }

    public isSupported(): boolean {
        /*
         * enable Hls if browser supports Hls.js and guest uses one of the following (due to autoplay)
         * - Desktop PC (non-Safari)
         * - Firefox >= 49 on Android
         * - Chrome >= 53 on Android (muted autoplay)
         */
        return (
            Hls.isSupported() &&
            !isBrowserVersion(BrowserName.SAFARI) &&
            !isBrowserVersion(BrowserName.FIREFOX, 0, BrowserOS.ANDROID, 49) &&
            !isBrowserVersion(BrowserName.CHROME, 0, BrowserOS.ANDROID, 53)
        );
    }

    public async create(
        callbacks: Callbacks,
        video?: HTMLVideoElement
    ): Promise<IPlayer> {
        let supportsPlayPromise = false;
        const dummyVideo = document.createElement('video');
        const promise = dummyVideo.play();

        if (promise) {
            supportsPlayPromise = true;
            promise.catch(() => {
                /** noop */
            });
        }

        let playMode = supportsPlayPromise
            ? PlayModes.PROMISE_PLAY_UNMUTED
            : PlayModes.AUTO_PLAY_UNMUTED;

        if (!video) {
            video = document.createElement('video');
            video.volume = 1;
            video.muted = false;
        }

        video.crossOrigin = 'anonymous';
        video.controls = false;

        video.style.width = '100%';
        video.style.height = '100%';

        if (!supportsPlayPromise) {
            video.autoplay = true;
        }

        if (this.config.poster) {
            video.poster = this.config.poster;
        }

        const hlsConfig = {
            debug: false,
            maxBufferLength: 3,
            maxMaxBufferLength: 10,
            capLevelToPlayerSize: true
        };

        let canvas: HTMLCanvasElement = null;

        if (
            this.config.useCanvasPoster ||
            this.config.useCanvasPoster === undefined
        ) {
            canvas = document.createElement('canvas');
            canvas.style.position = 'absolute';
            canvas.style.left = '-100000px';
            canvas.style.top = '0';
        }

        let quality: QualityLevel = 'low';
        let currentSource = '';
        let currentVideoWidth = 0;

        let hls: Hls = null;

        let sources = [];

        let hlsIsLoading = false;
        let streamPaused = false;

        let hlsAbortErrorRetry = false;
        let playTriggeredByUserInteraction = false;

        let playerVolume = video.volume;

        let canvasHideInterval = null;

        let autoPlayTimeout = null;
        let waitingTimeout = null;

        const sendPlayInfo = (): void => {
            callbacks.onPlayInfo({
                width: video.videoWidth,
                height: video.videoHeight,
                quality: quality,
                paused: video.paused,
                volume: video.volume,
                name: player.name,
                source: currentSource
            });
        };

        const videoResolutionChange = (videoWidth: number): void => {
            let newQuality: QualityLevel = 'low';
            const boundaries =
                this.config.qualityBoundaries || defaultQualityBoundaries;

            if (boundaries.good && videoWidth >= boundaries.good) {
                newQuality = 'good';
            } else if (boundaries.medium && videoWidth >= boundaries.medium) {
                newQuality = 'medium';
            }

            if (newQuality !== quality || videoWidth !== currentVideoWidth) {
                setQuality(newQuality);
            }

            currentVideoWidth = videoWidth;
        };

        const setQuality = (newQuality: QualityLevel): void => {
            quality = newQuality;
            sendPlayInfo();
        };

        const initHlsJs = (): void => {
            if (hls) {
                hls.destroy();
            }

            hls = new Hls(hlsConfig);

            hls.attachMedia(video);

            hls.on(Hls.Events.LEVEL_SWITCHED, () => {
                videoResolutionChange(video.videoWidth);
            });

            hls.on(Hls.Events.LEVEL_LOADED, () => {
                videoResolutionChange(video.videoWidth);
            });

            hls.on(Hls.Events.ERROR, (event, data) => {
                logger.log('Hls.Events.ERROR', data);

                // bufferStalledError
                // bufferSeekOverHole
                // bufferNudgeOnStall

                // levelLoadError

                const errorData = {
                    event: event
                };

                if (data.type) {
                    errorData['type'] = data.type;
                }

                if (data.fatal) {
                    errorData['fatal'] = true;
                }

                if (data.details) {
                    errorData['details'] = data.details;
                }

                callbacks.onPlayError(ErrorLevel.Info, errorData);

                if (data.details == 'bufferNudgeOnStall') {
                    // playNext()
                    return;
                }

                if (data.details == Hls.ErrorDetails.FRAG_LOAD_ERROR) {
                    logger.log('hls.ErrorDetails.FRAG_LOAD_ERROR');
                }

                if (data.fatal) {
                    switch (data.type) {
                        case Hls.ErrorTypes.NETWORK_ERROR:
                            // try to recover network error
                            logger.log(
                                'fatal network error encountered, try to recover'
                            );
                            hlsIsLoading = true;
                            hls.startLoad();
                            break;
                        case Hls.ErrorTypes.MEDIA_ERROR:
                            logger.log(
                                'fatal media error encountered, try to recover'
                            );
                            hls.recoverMediaError();
                            break;
                        default:
                            logger.log(
                                'fatal error encountered, cannot recover'
                            );
                            hls.destroy();
                            callbacks.onError('media');
                            break;
                    }
                }
            });

            hlsIsLoading = true;
            hls.loadSource(currentSource);
        };

        const onCanPlay = (): void => {
            if (!supportsPlayPromise) {
                autoPlayTimeout = setTimeout(() => {
                    if (!playTriggeredByUserInteraction) {
                        play(true);
                    } else {
                        logPlay({
                            name: 'AutoPlayError',
                            message: 'AutoPlay failed'
                        });

                        onError();
                    }
                }, 500);
            }
        };

        const onError = (): void => {
            let errorCode = 0;
            let errorMessage = '';

            if (video && video.error) {
                const errorExt: MediaErrorExt = video.error as MediaErrorExt;

                errorCode = errorExt.code;
                errorMessage = errorExt.message;
            }

            if (
                errorCode === MediaError.MEDIA_ERR_DECODE ||
                errorCode === MediaError.MEDIA_ERR_NETWORK
            ) {
                callbacks.onPlayError(ErrorLevel.Info, {
                    code: errorCode,
                    message: errorMessage
                });
                resumePlay();
            } else {
                callbacks.onError({ code: errorCode, message: errorMessage });
            }
        };

        const onPause = (): void => {
            if (!streamPaused) {
                // TODO: try to restart stream?
                callbacks.onSendMetrics(PlayerEvent.Pause);

                hlsIsLoading = false;
                hls.stopLoad();

                // TODO: pause can be triggered by user (e.g. via Android Phone: notification bar -> pause button @ stream -> pauses stream and can only be restarted from the notification bar)
                if (this.config.userInteractionCallback) {
                    this.config.userInteractionCallback(resumeByButton);
                }
            }
        };

        const onPlayError = (error: string | DOMException): void => {
            if (video && video.error) {
                onError();
            } else if (typeof error === 'string') {
                callbacks.onPlayError(ErrorLevel.Info, { message: error });
            } else if (typeof error === 'object') {
                callbacks.onPlayError(ErrorLevel.Info, {
                    name: error.name,
                    message: error.message
                });
            } else {
                callbacks.onError({ code: '1337', message: 'Unknown error' });
            }
        };

        const onPlaying = (): void => {
            if (autoPlayTimeout) {
                clearTimeout(autoPlayTimeout);
                autoPlayTimeout = null;
            }

            // force restart when stream lost focus and hence stopped load on hls
            if (!hlsIsLoading) {
                resumePlay();
            } else {
                callbacks.onSendMetrics(PlayerEvent.Playing);
                sendPlayInfo();

                // move the canvas
                if (canvasHideInterval) {
                    clearInterval(canvasHideInterval);
                }

                if (canvas) {
                    canvasHideInterval = setInterval(canvasHide, 25);
                }

                if (waitingTimeout) {
                    clearTimeout(waitingTimeout);
                    waitingTimeout = null;
                }
            }
        };

        const onTimeUpdate = (): void => {
            if (video.videoWidth > 0) {
                canvasUpdate();
            }
        };

        const onWaiting = (): void => {
            if (!streamPaused) {
                callbacks.onSendMetrics(PlayerEvent.Waiting);

                if (waitingTimeout) {
                    clearTimeout(waitingTimeout);
                }

                waitingTimeout = setTimeout(() => {
                    setQuality('low');
                }, this.config.waitingTimeout || defaultWaitingTimeout);
            }
        };

        const onWindowResize = (): void => {
            canvasUpdate();
        };

        const canvasUpdate = (): void => {
            if (canvas) {
                if (video.videoHeight > 0) {
                    canvas.height = video.clientHeight;
                    canvas.width = video.clientWidth;
                }

                window.requestAnimationFrame(canvasUpdateDo);
            }
        };

        const canvasUpdateDo = (): void => {
            if (canvas && video.readyState >= 2) {
                try {
                    // do not write directly to video's poster because it will be overwritten by hls.loadSource()
                    canvas
                        .getContext('2d')
                        .drawImage(
                            video,
                            0,
                            0,
                            video.clientWidth,
                            video.clientHeight
                        );
                } catch (error) {
                    if (error.name === NS_ERROR_NOT_AVAILABLE) {
                        // Wait a bit before trying again
                        setTimeout(canvasUpdateDo, 1);
                    } else {
                        throw error;
                    }
                }
            }
        };

        const canvasHide = (): void => {
            if (canvas && video.videoWidth) {
                canvas.style.left = '-100000px';

                clearInterval(canvasHideInterval);
                canvasHideInterval = null;
            }
        };

        const resumePlay = (): void => {
            if (canvas) {
                canvas.style.left = '0';
            }

            if (waitingTimeout) {
                clearTimeout(waitingTimeout);
            }

            waitingTimeout = setTimeout(() => {
                setQuality('low');
            }, this.config.waitingTimeout || defaultWaitingTimeout);

            // combination of both, hls.startLoad and play() is necessary in order to continue the stream (play) and to jump to the current position (startLoad)
            // re-init HLS in order to make sure HLS really fetches a fresh chunklist (avoids bufferStalled errors)
            // timeout is necessary to give canvas enough time to be put in place
            setTimeout(() => {
                initHlsJs();
                play(false);
            }, 100);

            callbacks.onSendMetrics(PlayerEvent.Load);
        };

        video.addEventListener('canplay', onCanPlay, true);
        video.addEventListener('pause', onPause, true);
        video.addEventListener('playing', onPlaying, true);
        video.addEventListener('timeupdate', onTimeUpdate, true);
        video.addEventListener('waiting', onWaiting, true);

        window.addEventListener('resize', onWindowResize);

        const logPlay = (error: Error = null): void => {
            const videoData = {
                muted: video.muted,
                paused: video.paused,
                volume: video.volume
            };

            const playerInfo: PlayPromiseInfo = {
                playMode: playMode,
                success: !error,
                videoElement: videoData,
                volume: playerVolume
            };

            if (error) {
                playerInfo.error = error.name;
                playerInfo.errorMsg = error.message;
            }

            callbacks.onSendMetrics(PlayerEvent.Play, playerInfo);

            if (this.config.playLogger) {
                this.config.playLogger(playerInfo);
            }
        };

        const play = (mute = false, button = false): void => {
            if (supportsPlayPromise) {
                video
                    .play()
                    .then(() => {
                        logPlay();
                    })
                    .catch((error) => {
                        logPlay(error);
                        if (
                            error.name === NOT_ALLOWED_ERROR ||
                            error.name === ABORT_ERROR
                        ) {
                            if (button) {
                                onPlayError(error);
                            } else if (
                                error.name == ABORT_ERROR &&
                                !hlsAbortErrorRetry
                            ) {
                                // retry once to cover occurring load() interrupts play()
                                hlsAbortErrorRetry = true;
                                setTimeout(() => {
                                    play();
                                }, 50);
                            } else if (!video.muted) {
                                playerVolume = 0;

                                video.muted = true;
                                video.volume = 0;

                                playMode = PlayModes.PROMISE_PLAY_MUTED;

                                callbacks.onVolumeChange(0);

                                play();
                            } else {
                                if (this.config.userInteractionCallback) {
                                    playMode = PlayModes.PROMISE_PLAY_BUTTON;

                                    this.config.userInteractionCallback(
                                        playByButton
                                    );
                                } else {
                                    onPlayError(
                                        'No userInteractionCallback set'
                                    );
                                }
                            }
                        } else {
                            onPlayError(error);
                        }
                    });
            } else {
                if (mute) {
                    logPlay({
                        name: 'AutoPlayError',
                        message: 'AutoPlay failed'
                    });

                    if (!video.muted) {
                        playerVolume = 0;

                        video.muted = true;
                        video.volume = 0;

                        playMode = PlayModes.AUTO_PLAY_MUTED;

                        callbacks.onVolumeChange(0);

                        video.play();
                    } else {
                        if (this.config.userInteractionCallback) {
                            playMode = PlayModes.AUTO_PLAY_BUTTON;

                            this.config.userInteractionCallback(playByButton);
                        } else {
                            onPlayError('No userInteractionCallback set');
                        }
                    }
                } else {
                    video.play();
                }
            }
        };

        const playByButton = (e): void => {
            if (e && e.cancelable) {
                e.preventDefault();
                e.stopPropagation();
            }

            playerVolume = 1;

            video.muted = false;
            video.volume = 1;

            callbacks.onVolumeChange(1);

            play(false, true);

            playTriggeredByUserInteraction = true;
        };

        const resumeByButton = (e): void => {
            if (e && e.cancelable) {
                e.preventDefault();
                e.stopPropagation();
            }

            resumePlay();
        };

        const playNext = (): void => {
            const source = sources.shift();
            if (source) {
                currentSource = source.stream;

                initHlsJs();

                callbacks.onPlayStart(source);

                sendPlayInfo();

                play();
            } else {
                callbacks.onError('source');
            }
        };

        const player: IPlayer = {
            el: [video, canvas],
            name: 'HLSJS',
            play: (sourceSet: SourceSet) => {
                sources = sourceSet.hls ? sourceSet.hls.slice() : [];

                playNext();

                if (streamPaused) {
                    streamPaused = false;
                }
            },

            stop: () => {
                if (canvas) {
                    canvas.style.left = '0';
                }

                video.pause();
                hls.stopLoad();
                callbacks.onPlayStop();

                hlsIsLoading = false;
                streamPaused = true;
            },

            destroy: () => {
                // do clean up since video element will be reused
                video.removeEventListener('canplay', onCanPlay, true);
                video.removeEventListener('pause', onPause, true);
                video.removeEventListener('playing', onPlaying, true);
                video.removeEventListener('timeupdate', onTimeUpdate, true);
                video.removeEventListener('waiting', onWaiting, true);

                window.removeEventListener('resize', onWindowResize);

                if (canvasHideInterval) {
                    clearInterval(canvasHideInterval);
                }

                player.stop();
                hls.destroy();

                // clean up video element
                video.crossOrigin = null;
                video.controls = false;
                video.style.width = null;
                video.style.height = null;
                video.removeAttribute('autoplay');

                return video;
            },

            setVolume: (value) => {
                playerVolume = value;

                video.volume = value;
                video.muted = !value;
            }
        };
        return player;
    }
}
