import { logger, PicUpStream, SourceSet, TargetSet } from 'vchat-core';

import { JpegTarget } from 'vchat-core';
import { Callbacks } from './callbacks';
import { CameraConfig } from './camera-config';
import { CameraPosition } from './camera-position';
import { FactoryQueueEntry } from './factory-queue-entry';
import { IPlayer } from './iplayer';
import { JpegUploadSettings } from './jpeg-upload-settings';
// import { MediaStreamFrameProvider } from './media-stream-frame-provider';
import { PlayerConfig } from './player-config';
import { PlayerFactory } from './player-factory';
import { PlayerHandler } from './player-handler';
import { createElement } from './utils';
import { VERSION } from './version';
import { VideoElementFrameProvider } from './video-element-frame-provider';

export const PlayerEvent = {
    Error: 'error',
    Init: 'init',
    Load: 'load',
    Pause: 'pause',
    Play: 'play',
    Playing: 'playing',
    QualityChange: 'qualityChange',
    Reload: 'reload',
    Waiting: 'waiting'
};

export const PlayModes = {
    /** promise play via button; the browser supports the newer promise return value of the play() function; the promise did not execute properly - neither unmuted nor muted; hence the play() call was triggered via user interaction */
    PROMISE_PLAY_BUTTON: 'uip',
    /** muted promise play; the browser supports the newer promise return value of the play() function; the promise could be executed successfully but only with the video element being muted (autplay started muted) */
    PROMISE_PLAY_MUTED: 'mpp',
    /** unmuted promise play; the browser supports the newer promise return value of the play() function; the promise could be executed successfully without muting the element (autoplay started with sound) */
    PROMISE_PLAY_UNMUTED: 'upp',
    /** user interaction; the browser only supports the older standard; the video element does not start automatically - neither unmuted nor muted; the play() call was triggered via user interaction */
    AUTO_PLAY_BUTTON: 'ui',
    /** muted autoplay; the browser only supports the older standard; the video could only be started muted */
    AUTO_PLAY_MUTED: 'map',
    /** unmuted autoplay; the browser only supports the older standard; the video could be started automatically with sound */
    AUTO_PLAY_UNMUTED: 'uap'
};

const defaultConstraints = {
    audio: false,
    video: {
        facingMode: 'user',
        width: 1920, // iOS Safari does not support ideal setting
        // tslint:disable-next-line: object-literal-sort-keys
        height: 1080
    }
};

const dummy = (): void => {
    /** noop */
};

function invalidateCallbacks(cb: Callbacks): void {
    for (const n in cb) {
        cb[n] = dummy;
    }
}

export class Player {
    public static readonly VERSION = VERSION;

    private _container: HTMLElement;
    private _playerContainer: HTMLDivElement;
    private _camContainer: HTMLDivElement;
    private _camVideo: HTMLVideoElement;
    private _camStream: MediaStream;
    private _camConfig: CameraConfig;
    private _camPublisher: PicUpStream;

    private _playing = false;

    private _publishing = false;

    private _sourceSet: SourceSet;
    private _targetSet: TargetSet;
    private _volume = 1;

    private _queue: string[] = [];
    private _defOrder: string[];

    private _player: IPlayer = null;
    // The currently selected stream format.
    private _format: string;
    private _handler: PlayerHandler;
    private _factory: PlayerFactory = null;
    private _callbacks: Callbacks;

    private _factoryMap: { [key: string]: PlayerFactory[] };
    private _failedFact: PlayerFactory[] = [];
    private _factoryQueue: FactoryQueueEntry[] = [];

    private _video: HTMLVideoElement;
    private _allowRearrange = false;

    constructor(config: PlayerConfig) {
        if (!config.handler) {
            throw new Error('handler must not be undefined.');
        }

        const c: HTMLDivElement = createElement('div', {
            width: '100%',
            // tslint:disable-next-line: object-literal-sort-keys
            height: '100%',
            position: 'relative'
        });

        config.container.append(c);
        this._container = c;
        this._video = config.video;

        this._playerContainer = createElement('div', {
            width: '100%',
            // tslint:disable-next-line: object-literal-sort-keys
            height: '100%'
        });
        this._camContainer = createElement('div', {
            backgroundColor: 'black',
            display: 'none',
            position: 'absolute'
        });

        c.append(this._playerContainer);
        c.append(this._camContainer);

        this._handler = config.handler;

        this._factoryMap = {};
        this._defOrder = [];
        config.factories.forEach((factory) => {
            const formats =
                typeof factory.format === 'string'
                    ? [factory.format]
                    : factory.format;
            formats.forEach((format) => {
                if (this._factoryMap[format] === undefined) {
                    this._factoryMap[format] = [];
                }

                this._factoryMap[format].push(factory);
                const index = this._defOrder.indexOf(format);
                if (index >= 0) {
                    this._defOrder.splice(index, 1);
                }
                this._defOrder.push(format);
            });
        });

        this._allowRearrange = config.preferExistingPlayer || false;
        logger.log(
            'Player.constructor complete',
            this._defOrder,
            this._factoryMap
        );

        if (config.video && config.video.volume !== this._volume) {
            this._volume = config.video.volume;
            this._handler.onVolumeChange(this._volume);
        }
    }

    public async initPlayer(format: string | string[]): Promise<void> {
        if (!this._player && this._queue.length === 0) {
            const formats =
                typeof format === 'string' ? [format] : format.slice();
            this._queue = formats.filter((value) => {
                let filtered = Object.prototype.hasOwnProperty.call(
                    this._factoryMap,
                    value
                );

                if (filtered) {
                    filtered =
                        this._factoryMap[value].filter(
                            (fact) => !this._failedFact.includes(fact)
                        ).length > 0;
                }

                return filtered;
            });

            this._createFactoryQueue();
            await this._createPlayer();
        }
    }

    get format(): string {
        return this._format;
    }

    /**
     * This function takes the source set and creates the best fitting player and starts playing the selected source.
     *
     * @param sourceSet - The sourceset describing all available streams. Usually retrieved using chat.startStream()
     */
    public async play(sourceSet: SourceSet): Promise<void> {
        this._sourceSet = sourceSet;

        if (sourceSet.preferredOrder && sourceSet.preferredOrder.length > 0) {
            this._queue = sourceSet.preferredOrder.slice();
        } else {
            this._queue = this._defOrder.slice();
        }

        this._queue = this._queue.filter((value) => {
            let filtered = Object.prototype.hasOwnProperty.call(
                this._factoryMap,
                value
            );

            if (filtered) {
                filtered =
                    this._factoryMap[value].filter(
                        (fact) => !this._failedFact.includes(fact)
                    ).length > 0;
            }

            return filtered && sourceSet[value] && sourceSet[value].length;
        });

        logger.log('Player.play', sourceSet, this._queue);

        if (this._player && this._format) {
            const index = this._queue.indexOf(this._format);
            if (index === 0 || (this._allowRearrange && index > 0)) {
                this._queue.splice(index, 1);
                this._player.play(sourceSet, this._format);
                return;
            }
        }

        this._createFactoryQueue();
        await this._createPlayer();
    }

    /**
     * Stops (pauses) the player, e.g. to be used when the vhcat-core calls an onChatPause() or when a Live Preview ends. The player will only stop pulling the streams but won't be destroyed - hence the latest frame will still be shown.
     */
    public stop(): void {
        if (this._player) {
            this._player.stop();
        }
    }

    /**  */
    get playing(): boolean {
        return this._playing;
    }

    /** Status of cam2cam */
    get publishing(): boolean {
        return this._publishing;
    }

    /**
     * This function takes the targetSet and starts the actual upstream.
     *
     * @param targetSet - information where the stream can be published to, usually retrieved by calling chat.startUpstream()
     *
     * @returns true on success, else false
     */
    public publish(targetSet: TargetSet): boolean {
        if (this._publishing === true) {
            if (this._targetSet !== targetSet) {
                throw new Error(
                    'cannot publish when still publishing to a different target set. Unpublish first'
                );
            } else {
                return true;
            }
        }

        this._targetSet = targetSet;

        if (this._player && this._player.publish && this._player.canPublish) {
            this._player.publish(targetSet);
        } else if (targetSet.jpeg && targetSet.jpeg.length > 0) {
            const jpegTarget = targetSet.jpeg[0] as JpegTarget;
            if (!this._camStream) {
                throw new Error(
                    'camera must be started first in order to publish the camera stream'
                );
            }

            if (!this._camPublisher) {
                this._camPublisher = this._createCamPublisher(jpegTarget);
            }

            this._camPublisher.publish(jpegTarget.stream, jpegTarget.fps);
            this._publishing = true;
            if (this._handler.onPublishStart) {
                this._handler.onPublishStart();
            }
        } else {
            return false;
        }

        return true;
    }

    /**
     * Stops sending the frames taken from the browser's camera.
     */
    public unpublish(): void {
        this._publishing = false;
        this._targetSet = null;
        if (this._camPublisher) {
            this._camPublisher.stop();
            this._camPublisher = null;
            if (this._handler.onPublishStop) {
                this._handler.onPublishStop();
            }
        }
        if (this._player && this._player.unpublish && this._player.canPublish) {
            this._player.unpublish();
        }
    }

    /**
     * Determines whether the browser has access to a camera.
     */
    public async hasCamera(): Promise<true | false | undefined> {
        if (this._player && this._player.hasCamera && this._player.canPublish) {
            return await this._player.hasCamera();
        } else if (
            navigator.mediaDevices &&
            navigator.mediaDevices.enumerateDevices
        ) {
            const devices = await navigator.mediaDevices.enumerateDevices();
            return devices.some((d) => d.kind === 'videoinput');
        } else {
            return false;
        }
    }

    /**
     * This function starts the browser's camera and shows the cam 2 cam overlay. Based on this function call you can retrieve the targetSet via chat.startUpstream(). Once you received the targetSet you can start publishing via player.publish(targetSet).
     * @param config - configuration params for the camera, see [CameraConfig] for more info.
     */
    public async startCamera(config?: CameraConfig): Promise<void> {
        this._camConfig = config || {};
        if (
            this._player &&
            this._player.startCamera &&
            this._player.canPublish
        ) {
            this._player.startCamera(config);
        } else {
            if (this._camVideo) {
                this._updateCameraPosition();
                this._updateCameraSize();

                if (this._handler.onCameraOn) {
                    this._handler.onCameraOn();
                }
            } else {
                try {
                    const mediaStream = await this._getUserMedia(
                        defaultConstraints
                    );

                    this._createCamVideo(mediaStream);
                } catch (error) {
                    if (
                        error.name &&
                        (error.name.startsWith('Permission') ||
                            error.name === 'NotAllowedError')
                    ) {
                        if (this._handler.onCameraDenied) {
                            this._handler.onCameraDenied();
                        }
                    } else {
                        if (this._handler.onCameraOff) {
                            this._handler.onCameraOff();
                        }
                    }
                }
            }
        }
    }

    /**
     * Stops the use of the browser's camera.
     */
    public stopCamera(): void {
        this._camConfig = null;
        if (this._camVideo) {
            // http://stackoverflow.com/questions/11642926/stop-close-webcam-which-is-opened-by-navigator-getusermedia
            if (this._camStream) {
                if (
                    this._camStream.getVideoTracks &&
                    this._camStream.getVideoTracks()
                ) {
                    this._camStream
                        .getVideoTracks()
                        .forEach((track) => track.stop());
                } else if (this._camStream.stop) {
                    this._camStream.stop();
                }

                this._camStream = null;
            }

            this._camContainer.style.display = 'none';
            this._camContainer.removeChild(this._camVideo);

            if (this._camVideo !== undefined) {
                this._camVideo.srcObject = null;
            } else {
                this._camVideo.src = '';
            }

            this._camVideo = null;
            if (this._handler.onCameraOff) {
                this._handler.onCameraOff();
            }
        }
        window.removeEventListener('resize', this);

        if (
            this._player &&
            this._player.stopCamera &&
            this._player.canPublish
        ) {
            this._player.stopCamera();
        }
    }

    /** sets the volume of the player */
    set volume(value: number) {
        value = Math.min(Math.max(value, 0), 1);
        if (this._player) {
            this._player.setVolume(value);
        }
        if (this._volume !== value) {
            this._volume = value;
            if (this._handler.onVolumeChange) {
                this._handler.onVolumeChange(value);
            }
        }
    }

    /** gets the volume of the player
     *
     * @returns The volume in the range [0.0, 1.0]
     */
    get volume(): number {
        return this._volume;
    }

    /** Forces the player to switch to the next best player. */
    public async playNext(): Promise<void> {
        this._destroyPlayer();
        await this._createPlayer();
    }

    /** Destroys the player.
     * @returns HTML5 video element if set (HLS.js Player / HLS native Player only) for future use (on next player instantiation).
     */
    public destroy(): HTMLVideoElement {
        logger.log('Player.destroy');
        this.unpublish();
        this.stopCamera();
        this._destroyPlayer();

        const parent = this._container.parentNode;
        if (parent) {
            parent.removeChild(this._container);
        }

        return this._video;
    }

    public handleEvent(event: Event): void {
        if (event.type === 'resize') {
            this._updateCameraSize();
        }
    }

    private _createFactoryQueue(): void {
        this._queue.map((format) => {
            if (this._factoryMap[format] !== undefined) {
                this._factoryMap[format].map((fact) => {
                    this._factoryQueue.push({
                        format,
                        factory: fact
                    } as FactoryQueueEntry);
                });
            }
        });
    }

    private async _createPlayer(): Promise<void> {
        let format: string = null;
        let factoryQueueEntry = this._factoryQueue.shift();

        while (factoryQueueEntry) {
            format = factoryQueueEntry.format;

            if (this._player) {
                if (this._format === format) {
                    return Promise.resolve();
                } else {
                    this._destroyPlayer();
                }
            }

            const factory = factoryQueueEntry.factory;
            this._factory = factory;

            if (
                factory &&
                !this._failedFact.includes(factory) &&
                factory.isSupported()
            ) {
                this._format = format;

                if (this._handler.onPlayerSwitch) {
                    this._handler.onPlayerSwitch(format);
                }

                try {
                    const callbacks = this._createCallbacks(factory);
                    const created = factory.create(callbacks, this._video);

                    const stillValid = (): boolean =>
                        this._factory === factory && this._format === format;

                    try {
                        const p = await created;

                        if (stillValid()) {
                            this._installPlayer(p);
                            return Promise.resolve();
                        } else {
                            invalidateCallbacks(callbacks);
                        }
                    } catch (error) {
                        logger.warn(
                            'Player._createPlayer Promise.catch',
                            error
                        );

                        this._failedFact.push(factory);
                        invalidateCallbacks(callbacks);

                        if (stillValid()) {
                            this._destroyPlayer();
                            return this._createPlayer();
                        } else {
                            return Promise.resolve();
                        }
                    }
                } catch (error) {
                    logger.warn('Player._createPlayer error', error);
                }
            }

            factoryQueueEntry = this._factoryQueue.shift();
        }
    }

    private _getUserMedia(
        constraints?: MediaStreamConstraints
    ): Promise<MediaStream> {
        if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
            return navigator.mediaDevices.getUserMedia(constraints);
        } else if (navigator.getUserMedia) {
            return new Promise<MediaStream>((resolve, reject) => {
                navigator.getUserMedia(
                    constraints,
                    (mediaStream: MediaStream) => {
                        resolve(mediaStream);
                    },
                    (e: Error) => {
                        reject(e);
                    }
                );
            });
        } else if (navigator['webkitGetUserMedia']) {
            return new Promise<MediaStream>((resolve, reject) => {
                navigator['webkitGetUserMedia'](
                    constraints,
                    (mediaStream: MediaStream) => {
                        resolve(mediaStream);
                    },
                    (e: Error) => {
                        reject(e);
                    }
                );
            });
        } else {
            return Promise.reject(new Error('No GetUserMedia API available'));
        }
    }

    private _installPlayer(player: IPlayer): void {
        // assert player != null
        this._player = player;

        for (const playerElement of player.el) {
            // to avoid adding optional elements which can be null
            if (playerElement) {
                this._playerContainer.append(playerElement);
            }
        }

        if (this._handler.onPlayerSwitchComplete) {
            this._handler.onPlayerSwitchComplete(this._format);
        }

        this._callbacks.onSendMetrics(PlayerEvent.Init);

        player.setVolume(this._volume);
        if (this._sourceSet) {
            player.play(this._sourceSet, this._format);
        }
    }

    private _createCallbacks(factory: PlayerFactory): Callbacks {
        return (this._callbacks = {
            onVolumeChange: (value: number): void => {
                if (this._volume !== value && this._handler.onVolumeChange) {
                    this._volume = value;
                    this._handler.onVolumeChange(value);
                }
            },
            onError: (error): void => {
                this._callbacks.onSendMetrics(PlayerEvent.Error, { error });

                logger.warn('Player.onPlayerError', error);
                this._playing = false;
                this._failedFact.push(factory);
                this.playNext();
            },

            onSendMetrics: (event, info = null): void => {
                if (info === null) {
                    info = {};
                }

                const metrics = {
                    playerEvent: event,
                    playerFormat: this._format,
                    playerType: this._player ? this._player.name : '',
                    ...info
                };

                const connection =
                    navigator['connection'] ||
                    navigator['mozConnection'] ||
                    navigator['webkitConnection'] ||
                    navigator['msConnection'] ||
                    {};

                if (connection) {
                    const networkInfo = {};
                    const networkKeys = [
                        'downlink',
                        'downlinkMax',
                        'effectiveType',
                        'rtt',
                        'type'
                    ];

                    for (const n of networkKeys) {
                        if (connection[n]) {
                            networkInfo[n] = connection[n];
                        }
                    }

                    metrics['networkInfo'] = networkInfo;
                }

                // TODO: in first iteration send data only via handler so it can be sent via chatInstance
                // in a second step: create an external interface to connect to via a trackingID (e.g. userId, ChatId)
                if (this._handler.onSendMetrics) {
                    this._handler.onSendMetrics(metrics);
                }
            },

            onPlayStart: (source): void => {
                // when event is .Play -> play() has been executed
                this._callbacks.onSendMetrics(PlayerEvent.Play);

                if (this._handler.onPlayStart) {
                    this._handler.onPlayStart(source);
                }
            },
            onPlayStop: (): void => {
                this._callbacks.onSendMetrics(PlayerEvent.Pause);

                this._playing = false;
                if (this._handler.onPlayStop) {
                    this._handler.onPlayStop();
                }
            },
            onPlayInfo: (info): void => {
                if (this._handler.onPlayInfo) {
                    this._handler.onPlayInfo(info);
                }
            },

            onPlayError: (level, error): void => {
                this._callbacks.onSendMetrics(PlayerEvent.Error, {
                    error: error
                });

                if (this._handler.onPlayError) {
                    this._handler.onPlayError(level);
                }
            },
            onCameraOn: (): void => {
                if (this._handler.onCameraOn) {
                    this._handler.onCameraOn();
                }
            },
            onCameraOff: (): void => {
                if (this._handler.onCameraOff) {
                    this._handler.onCameraOff();
                }
            },
            onCameraDenied: (): void => {
                if (this._handler.onCameraDenied) {
                    this._handler.onCameraDenied();
                }
            },

            onPublishStart: (): void => {
                if (this._handler.onPublishStart) {
                    this._handler.onPublishStart();
                }
            },
            onPublishStop: (): void => {
                if (this._handler.onPublishStop) {
                    this._handler.onPublishStop();
                }
            }
        });
    }

    private _destroyPlayer(): void {
        logger.log('Player._destroyPlayer', this._player, this._format);

        const player = this._player;
        if (player) {
            const video = player.destroy();

            if (video) {
                this._video = video;
            }

            for (const playerElement of player.el) {
                if (
                    playerElement &&
                    playerElement.parentNode === this._playerContainer
                ) {
                    playerElement.remove();
                }
            }
        }

        if (this._callbacks) {
            invalidateCallbacks(this._callbacks);
        }

        this._player = null;
        this._callbacks = null;
        this._factory = null;
        this._format = null;
        this._playing = false;
    }

    private _createCamVideo(mediaStream: MediaStream): void {
        logger.log('Player.getUserMedia success getCapabilities', mediaStream);

        this._camStream = mediaStream;

        let video: HTMLVideoElement = this._camVideo;

        if (!video) {
            video = createElement('video', {
                width: '100%',
                height: '100%',
                border: 'none'
            });
            video.muted = true;
            video.volume = 0;
            video.autoplay = true;

            // special to allow autoplay in iOS
            video.setAttribute('playsinline', 'true');

            this._camContainer.append(video);
        }

        this._camVideo = video;

        if (video.srcObject !== undefined) {
            video.srcObject = mediaStream;
        } else {
            video.src = URL.createObjectURL(mediaStream);
        }

        video.onloadedmetadata = (): void => {
            if (video.videoWidth) {
                this._camContainer.style.display = 'block';
                this._updateCameraPosition();
                this._updateCameraSize();

                window.addEventListener('resize', this);

                if (this._handler.onCameraOn) {
                    this._handler.onCameraOn();
                }
            } else {
                // setTimeout()
            }
        };
    }

    private _updateCameraPosition(): void {
        const s = this._camContainer.style;
        const p = ['top', 'left', 'right', 'bottom'];
        p.forEach((prop) => s.removeProperty(prop));

        const margin = 10;

        switch (this._camConfig.position) {
            case CameraPosition.FullScreen: {
                s.top = '0px';
                s.left = '0px';
                break;
            }

            case CameraPosition.BottomRight: {
                s.bottom = margin + 'px';
                s.right = margin + 'px';
                break;
            }

            case CameraPosition.BottomLeft: {
                s.bottom = margin + 'px';
                s.left = margin + 'px';
                break;
            }

            case CameraPosition.TopRight: {
                s.top = margin + 'px';
                s.right = margin + 'px';
                break;
            }

            case CameraPosition.TopLeft:
            default: {
                s.top = margin + 'px';
                s.left = margin + 'px';
                break;
            }
        }
    }

    private static computeCam2CamVideoSize(
        camConfig: CameraConfig,
        aspectRation: number,
        container: HTMLElement
    ): [number, number] {
        let w = 0;
        let h = 0;

        const cw = container.clientWidth;
        const ch = container.clientHeight;

        if (camConfig.position === CameraPosition.FullScreen) {
            return [cw, ch];
        }

        if (camConfig.width) {
            w = camConfig.width;
            h = Math.round(w / aspectRation);
        } else if (camConfig.height) {
            h = camConfig.height;
            w = Math.round(h * aspectRation);
        } else {
            const wRatio = camConfig.widthRatio || 3.5;
            const hRatio = camConfig.heightRatio || 2; // 2.5
            const i: number = Math.floor(
                Math.min(cw / wRatio / 80, ch / hRatio / (80 / aspectRation))
            );
            w = i > 2 ? i * 80 : 160;
            h = Math.round(w / aspectRation);
        }

        return [w, h];
    }

    private _updateCameraSize(): void {
        if (!this._camVideo) {
            return;
        }

        const aspectRation: number =
            this._camVideo.videoWidth / this._camVideo.videoHeight;

        const [w, h] = Player.computeCam2CamVideoSize(
            this._camConfig,
            aspectRation,
            this._container
        );

        const s = this._camContainer.style;
        s.width = w + 'px';
        s.height = h + 'px';
    }

    private _createCamPublisher(jpegTarget: JpegTarget): PicUpStream {
        const jpegUploadSettings: JpegUploadSettings = {
            width: jpegTarget.width,
            height: jpegTarget.height,
            quality: jpegTarget.quality
        };
        const provider = new VideoElementFrameProvider(
            this._camVideo,
            jpegUploadSettings,
            (error: Error) => {
                logger.warn('Player.PicUpStream.onError', error);
                this.unpublish();
            }
        );

        return new PicUpStream(provider);
    }
}
